import React, { useEffect, useState } from "react";
import { Button, Form, InputGroup } from "react-bootstrap";
const App = () => {
    const [fields, setFields] = useState(null);
    const [isDisabled, setIsDisabled] = useState(false);
    useEffect(() => {
        chrome.storage.sync.get(["fields"], (result) => {
            setFields(result.fields);
        });
    }, []);
    const FieldAddHandler = (e) => {
        e.preventDefault();
        if (fields && fields.length !== 0) {
            let tempFields = [...fields];
            tempFields.push("");
            setFields(tempFields);
        }
        else {
            setFields([""]);
        }
    };
    const FieldDeleteHandler = (e, ind) => {
        e.preventDefault();
        if (fields) {
            let tempFields = [...fields];
            tempFields.splice(ind, 1);
            setFields(tempFields);
        }
    };
    const FieldChangeHandler = (e, ind) => {
        e.preventDefault();
        if (fields) {
            let tempFields = [...fields];
            tempFields[ind] = e.target.value;
            setFields(tempFields);
        }
    };
    const SaveFieldsHandler = (e) => {
        e.preventDefault();
        setIsDisabled(true);
        let tempFields = [];
        if (fields) {
            fields.map((field) => {
                if (field !== "") {
                    tempFields.push(field);
                }
            });
            chrome.storage.sync.set({ fields: tempFields });
            setFields(tempFields);
        }
        setTimeout(() => {
            setIsDisabled(false);
        }, 1000);
    };
    return (React.createElement("div", { className: "m-5" },
        React.createElement("h4", null, "Form Builder"),
        React.createElement(Form, null,
            React.createElement(Form.Group, { className: "mb-3", controlId: "exampleForm.ControlInput1" },
                React.createElement(Form.Label, null, "Fields"),
                fields && fields.length > 0 ? (fields.map((field, ind) => (React.createElement(InputGroup, { key: `field_${ind}`, className: "mb-3" },
                    React.createElement(Button, { variant: "danger", onClick: (e) => {
                            FieldDeleteHandler(e, ind);
                        }, disabled: isDisabled },
                        React.createElement("i", { className: "fa-solid fa-x" })),
                    React.createElement("input", { className: "form-control", placeholder: "empty", disabled: isDisabled, value: field, onChange: (e) => {
                            FieldChangeHandler(e, ind);
                        } }))))) : (React.createElement("p", null, "Please click plus button to add fields."))),
            React.createElement("div", { className: "text-center" },
                React.createElement(Button, { onClick: (e) => {
                        FieldAddHandler(e);
                    }, disabled: isDisabled },
                    React.createElement("i", { className: "fa-solid fa-plus" }))),
            React.createElement("div", { className: "text-center my-3" },
                React.createElement(Button, { variant: "success", onClick: (e) => SaveFieldsHandler(e), disabled: isDisabled }, isDisabled ? "Saving..." : "Save the changes...")))));
};
export default App;
